@import url("https://fonts.googleapis.com/css?family=Open+Sans:300,400,700");
html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background-color: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  box-sizing: border-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  box-sizing: border-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

body {
  font-family: 'Open Sans', sans-serif;
  background: #a1a1a1; }

a {
  text-decoration: none; }

.container {
  width: 73.4375vw;
  margin: 0 auto; }

.wrapper {
  width: 73.4375vw;
  display: flex;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: #fff; }

.menu {
  width: 22.5%;
  background: #303841; }
  .menu__title {
    font-size: 28px;
    font-weight: 300;
    color: #ffc12d;
    padding: 10px 0 0 25px; }
  .menu__list {
    padding-left: 0;
    margin-top: 45px; }
  .menu__link {
    display: block;
    width: 100%;
    font-size: 0.88542vw;
    padding: 16px 0 16px 10px;
    margin: 0 15px;
    position: relative;
    color: #fff;
    transition: background .2s ease-in-out;
    border-bottom: 1px solid #3a414a; }
    .menu__link.beforeActive-js {
      border-bottom: 1px solid #442a9d;
      margin: 0;
      padding-left: 25px; }
  .menu__item {
    display: flex;
    align-items: center;
    font-weight: 100;
    list-style-type: none;
    position: relative;
    transition: background .2s ease-in-out; }
    .menu__item:before {
      content: '';
      position: absolute;
      width: 5px;
      height: 101%;
      left: 0;
      top: -.5%;
      background: #303841;
      transition: background .2s ease-in-out; }
    .menu__item:hover {
      background: #442a9d; }
      .menu__item:hover:before {
        background: #ffc12d; }
      .menu__item:hover .menu__link {
        border-bottom: 1px solid #442a9d; }
  .menu__img {
    margin-right: 20px; }

.info {
  width: 77.5%;
  font-weight: 600; }
  .info__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 0;
    border-bottom: 2px solid #f8f8f8; }
  .info__customer {
    font-size: 0.83333vw;
    font-weight: 600;
    display: flex;
    margin-left: 2.08333vw; }
    .info__customer .title {
      margin-right: 2.60417vw;
      position: relative; }
      .info__customer .title:after {
        content: '';
        position: absolute;
        top: 50%;
        right: -1.82292vw;
        transform: translateY(-50%);
        width: 20px;
        height: 20px;
        background: url("../img/arrow.png") center no-repeat;
        background-size: 0.52083vw; }
    .info__customer .name {
      color: #442a9d; }
  .info__search .form {
    position: relative;
    width: 14.58333vw;
    margin-left: 12.5vw; }
    .info__search .form__input {
      width: 100%;
      font-size: 0.625vw;
      font-weight: 300;
      padding: 10px 20px;
      border: 1px solid #f8f8f8;
      border-radius: 3px; }
    .info__search .form__btn {
      position: absolute;
      top: 50%;
      right: 12px;
      transform: translateY(-50%);
      width: 20px;
      height: 20px;
      background: url("../img/search.png") center no-repeat;
      border: none; }
  .info__notification {
    width: 25px;
    position: relative;
    margin: 0 1.04167vw; }
    .info__notification .counter {
      position: absolute;
      top: -5px;
      right: -4px;
      font-size: 11px;
      padding: .5px 4px;
      color: #fff;
      background: #f14e95;
      border-radius: 50%; }
  .info__support {
    display: flex;
    align-items: center; }
    .info__support .name {
      font-size: 0.83333vw;
      margin: 0 1.5625vw 0 0.625vw; }
  .info__person {
    display: flex;
    padding-top: 58px; }
    .info__person .img {
      padding: 0 40px 0 35px; }
    .info__person .data {
      width: 100%;
      margin-top: -5px; }
      .info__person .data__name {
        display: flex;
        align-items: center; }
        .info__person .data__name .name {
          font-size: 20px;
          margin-right: 20px;
          display: inline-block;
          color: #4f4f4f; }
        .info__person .data__name .status {
          display: inline-block;
          font-size: 11px;
          padding: 2px 9px;
          color: #fff;
          background: #ffc12d;
          border-radius: 3px; }
      .info__person .data__job {
        font-size: 14px;
        padding: 5px 0 15px;
        border-bottom: 2px solid #f8f8f8;
        color: #999999; }
      .info__person .data__contacts {
        display: flex;
        align-items: center;
        padding-top: 15px;
        font-size: 0.77083vw; }
        .info__person .data__contacts .title {
          font-size: 0.64063vw;
          color: #999999;
          margin-bottom: 5px; }
        .info__person .data__contacts .phone a {
          color: #442a9d; }
        .info__person .data__contacts .address {
          color: #442a9d;
          margin-left: 8.07292vw; }
        .info__person .data__contacts .btn {
          font-size: 0.70313vw;
          padding: 8px 25px;
          margin: 0 1.5625vw 0 13.80208vw;
          color: #ffc12d;
          background: #303841;
          border: none;
          border-radius: 4px;
          transition: background .2s ease-in-out, box-shadow .2s ease-in-out; }
          .info__person .data__contacts .btn:hover {
            background: #222241;
            box-shadow: 0px 10px 10px rgba(34, 34, 65, 0.2); }
  .info__history {
    margin: 80px 0 30px 40px; }
    .info__history .item {
      margin-right: 27px;
      padding: 0 15px 30px;
      position: relative;
      font-size: 0.72917vw;
      color: #303841; }
      .info__history .item:after {
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        width: 0;
        height: 5px;
        background: #442a9d;
        border-radius: 5px;
        transition: width .2s ease-in-out; }
      .info__history .item:hover:after {
        width: 100%;
        left: 0; }
  .info__other {
    padding: 105px 0;
    background: #ededed; }

@media only screen and (max-width: 1800px) {
  .info__search .form {
    margin-left: auto; }
  .info .data__contacts .address, .info .data__contacts .btn {
    margin: auto; } }
